*{
  margin: 0;
  padding: 0;
  /* box-sizing: border-box; */
  font-family: sans-serif;
}
body
{
  background-color: #011926;
  color: #245c69;
}
.box
{
  /* border: 2px solid red; */
  height: 100vh;
  display: flex;
  flex-direction: column;
  gap: 40px;
}
.box > *{
  flex-grow: 1;
}
p
{
  width: 75%;
  min-height: 200px;
  margin: 0px auto;
  background-color: #011926;
  padding:20px;
  line-height: 30px;
  border-radius: 9px;

}
h1
{
  text-align: center;
  margin-top: 10px;
  letter-spacing: 2px;
}
input
{
  display: block;
  margin: 0px auto;
  height: 55px;
  
  width: 300px;
  border: 0;
  outline: 0;
  font-size: 1.2rem;
  border-radius: 9px;
  font-family: sans-serif;
  text-align: center;
  background-color: #245c69;
  color: white;
}
.buttons
{
  /* border: 2px solid red; */
  height:0px;
  display: flex;
  justify-content: space-around;
  align-items: center;
}
.active
{
  background-color: rgb(71, 75, 68);
  color: black;
}
.buttons > *{
  height: 40px;
  width: 90px;
  border-radius: 9px;
  outline: none;
  background-color: rgba(13, 139, 131, 0.195);
}
.correct
{
  color: green;
}
.incorrect
{
  color: red;
}
pre
{
  display: inline-block;
  font-size: 1.4rem;
}
.info
{
  display: flex;
  justify-content: space-around;
  font-size: 1.4rem;
}
.info > span
{
  /* border: 2px solid red; */
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 20px;
  font-weight: 400;
  font-size: 2rem;
}
.hardMode
{
  animation-name:coder;
  animation-duration:1s;
  animation-iteration-count: infinite;
  animation-fill-mode: forwards;
  animation-timing-function: ease;
  display: inline-block;
}
.reset
{
  background-color:rgb(171, 48, 48);
  font-size: 1.2rem;
  color: white;
  border: 0;
}
.newpara
{
  background-color: #428bca;
  font-size: 1.1rem;
  color: white;
  border: 0;
}
.hardmode
{
  font-size: 1rem;
  color: white;
  border: 0;
  background-color: rgb(43, 43, 47);
}
@keyframes coder {
0%
{
  transform: translateX(10px)
}
50%
{
  transform: translateX(-20px)
}
60%
{
  transform: translateX(10px)
}
100%
{
  transform: translateX(20px)
}
  
}
.wpm
{
  font-weight: 900;
  color: green;
}